<script>
import { reactive, onMounted, getCurrentInstance } from 'vue';
import longTextTpl from './longTextTpl/longTextTpl.vue';
import catalogNav from './longText/CatalogNav.vue';
import { myTips } from '@/utils/common.js';
import axios from 'axios';
export default {
  components: {
    longTextTpl,
    catalogNav
  },
  name: '',
  setup() {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      webUrlStr: '', //默认数据获取地址
      isUrlInputShow: false, //地址输入框是否显示
      oldCatalogEle: null, //保存旧的目录元素
      hierarchyNum: 0, //目录层级
      interceptionNum: 20, //截取个数
      setShow: false, //配置显示
      fileList: null, //文件元素
      getFileList: '',
      longKeyWordObj: null, //需要传递的查询词
      queryKeyWord: '', //输入框查询词
      queryType: '0', //查询类型
      queryTypeNav: [
        { text: '同篇', sign: '0' },
        { text: '同段', sign: '1' },
        { text: '同句', sign: '2' }
      ],
      Ktotal: 0,
      Knum: 0,
      switchHighObj: null, //上下切换对象
      switchChange: true, //上下切换改变状态
      navType: '0', //0 目录  1 定位
      longTextCatalog: [], //目录
      matching: [] //匹配定位目录
    });
    onMounted(() => {});
    const getData = () => {
      data.isUrlInputShow = false;
      // 请求远程html地址获取dom内容
      // http://analyze.doc.rongdasoft.com/filepre/2022/2022-04-27/339c5f9b7c0582ac926e6fdc6830a2b9.html
      // 使用代理的路径 默认html数据 '/api1/filepre/2022/2022-04-27/339c5f9b7c0582ac926e6fdc6830a2b9.html';
      // 完整路径直接使用 用户直接输入 https://rd-xp-test-pdfhtml.rongdasoft.com/filek/2022/2022-03-29/0de15a371c783827866cb0a5ea0c007f.html
      // 默认读取本地文件
      const url = data.webUrlStr || '/有目录格式文件002.html';
      axios
        .get(url)
        .then((res) => {
          data.getFileList = res.data; //放到长文本渲染
          data.webUrlStr = '';
        })
        .catch((err) => {
          myTips('error', '地址或数据格式错误~');
        });
    };
    // 文件变化
    const choosefile = () => {
      const fileList = document.getElementById('files').files[0];
      data.fileList = fileList;
    };
    // 关键词变化
    const startQuery = () => {
      data.longKeyWordObj = {
        queryKeyWord: data.queryKeyWord, //关键词
        queryType: data.queryType, //类型
        interceptionNum: parseInt(data.interceptionNum)
      };
    };
    // 目录点击长文本中标题高亮（定位再目录组件实现）
    const catalogId = (ele) => {
      // 清除老元素背景
      if (data.oldCatalogEle) {
        data.oldCatalogEle.style.backgroundColor = 'transparent'; // 清除背景色
      }
      ele.style.backgroundColor = '#ddd'; //点击目录传递元素出去，方便长文本中目录内容高亮（待开发）bug 不是统计目录会同时高亮多个
      data.oldCatalogEle = ele;
    };
    // 获取查询的总数
    const getKtotal = (num) => {
      data.Ktotal = num;
    };
    // 获取当前位置
    const getKnum = (num) => {
      data.Knum = num;
    };
    // 上下切换高亮
    const switchHighlight = (type, index) => {
      data.switchHighObj = {
        type,
        index,
        change: !data.switchChange
      };
    };
    // 获取目录
    const getLongTextCatalog = (catalog) => {
      if (catalog.length) {
        data.navType = '0';
      }
      data.longTextCatalog = catalog;
    };
    // 获取匹配定位目录
    const getCatalogue = (catalogue) => {
      if (catalogue.length) {
        data.navType = '1';
      }
      data.matching = catalogue;
    };

    return {
      getData,
      choosefile,
      data,
      startQuery,
      getKtotal,
      getKnum,
      switchHighlight,
      getLongTextCatalog,
      getCatalogue,
      catalogId
    };
  }
};
</script>
