.center {
  line-height: 22px;
  padding: 20px 20px;
  text-align: justify;
  table {
    position: relative;
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    text-align: center;
    color: rgb(51, 51, 51);
    font-size: 12px;
    border: 0px;
    margin: 10px 0;
    transition: 0.3s;
    tbody {
      border: 1px solid #666;
      td {
        height: 36px;
      }
    }
    &:hover {
      cursor: pointer;
      box-shadow: 5px 5px 5px rgb(158, 158, 158);
    }
    .copy {
      outline: none;
      cursor: pointer;
      position: absolute;
      width: 66px;
      height: 30px;
      top: -25px;
      right: -20px;
      line-height: 30px;
      border-radius: 4px;
      color: white;
      border: 0px;
      opacity: 0.5;
      background: #ff8800;
      &:hover {
        opacity: 1;
      }
    }
  }
  /*文档内容清洗样式*/
  h1 a,
  h1 span {
    font-size: 16px !important;
    font-weight: bold !important;
  }
  /*二级目录*/
  h2 a,
  h2 span {
    font-size: 14px !important;
    font-weight: bold !important;
  }
  /*三级目录*/
  h3 a,
  h3 span {
    font-size: 14px !important;
    font-weight: normal !important;
  }
  /*3.8.2.7的公告详情页标题样式*/
  h1 {
    padding-left: 0 !important;
    text-align: left !important;
    text-indent: 0 !important;
    font-size: 16px;
    font-weight: bold;
  }
  h2 {
    padding-left: 0 !important;
    text-align: left !important;
    text-indent: 0 !important;
    font-size: 14px;
    font-weight: bold;
  }
  h3,
  h4,
  h5,
  h6 {
    padding-left: 0 !important;
    text-align: left !important;
    text-indent: 28px !important;
    font-size: 14px;
    font-weight: normal !important;
  }
  /*正文里面的p和span标签*/
  p,
  span {
    text-indent: 28px !important;
    padding: 0 5px;
    font-weight: normal !important;
    font-size: 14px;
  }
  p {
    padding: 5px;
  }
}
</style>
