<template>
  <div>
    <div v-for="item in catalogArr" class="catalog-item" :key="item.id">
      <!-- 显示内容 -->
      <p :title="item.text" class="grade">
        <!-- 图标 -->
        <span
          @click="isOpen(item)"
          class="arrow iconfont"
          :class="
            !item.children.length
              ? 'no-show'
              : data.actNavOpen.includes(item.id)
              ? 'icon-zhedie'
              : 'icon-zhankai'
          "
        ></span>
        <!-- 内容 -->
        <span
          class="catalog-text"
          :class="{ high: data.actNavHigh === item.id }"
          @click="catalogLocation(item.id)"
        >
          {{ item.text }}
        </span>
      </p>
      <!-- 递归目录 data.actNavOpen.includes(item.id) 控制默认展开收起-->
      <h-transitionH>
        <catalogNav
          v-show="data.actNavOpen.includes(item.id) !== isAllOpen"
          v-if="item.children.length"
          :onlyOpen="onlyOpen"
          :isAllOpen="isAllOpen"
          :catalogArr="item.children"
          @catalogId="catalogId"
        ></catalogNav>
      </h-transitionH>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
export default {
  name: 'catalogNav',
  props: {
    // 目录数据
    catalogArr: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 控制是否同时只能展开一个（全部展开时，此属性失效）
    onlyOpen: {
      type: Boolean,
      default: false
    },
    // 控制初始化目录是否全部展开
    isAllOpen: {
      type: Boolean,
      default: false
    }
  },
  setup(props, ctx) {
    const data = reactive({
      actNavOpen: [], //保存选中的id展开
      actNavHigh: '', //点击选中目录
      myOnlyOpen: props.isAllOpen ? false : props.onlyOpen   //控制同时展开一个，全部展开时，只打开一个失效
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    //保存展开的导航id
    const isOpen = (navObj) => {
      if (!navObj.children.length) return; //无子级不保存
      let navIndex = data.actNavOpen.indexOf(navObj.id); //是否存在id获取下标
      if (navIndex == -1) {
        if (data.myOnlyOpen) {
          data.actNavOpen = [navObj.id];
        } else {
          data.actNavOpen.push(navObj.id); //不存在就追加
        }
      } else {
        data.actNavOpen.splice(navIndex, 1); //存在就删除
      }
    };

    // 定位跳转位置
    const catalogLocation = (id) => {
      let ele = document.querySelector(`#${id}`);
      ele.scrollIntoView({
        // block : "center",   //可指定上中下
        behavior: 'smooth' // 平滑过渡
      });
      ctx.emit('catalogId', ele); //递归自己传递出去
    };
    // 传递点击ele用于父级长文本高亮
    const catalogId = (ele) => {
      ctx.emit('catalogId', ele);
    };
    return {
      catalogLocation,
      isOpen,
      data,
      catalogId
    };
  }
};
</script>
<style scoped lang="less">
.catalog-item {
  cursor: pointer;
  font-size: 14px;
  padding: 2px;
  padding-left: 15px;
  .grade {
    overflow: hidden; /*内容超出后隐藏*/
    text-overflow: ellipsis; /* 超出内容显示为省略号 */
    white-space: nowrap; /* 文本不进行换行 */
    .arrow {
      color: #999;
      &.no-show {
        padding-left: 16px;
      }
      &:hover {
        color: rgb(0, 125, 241);
      }
    }
    .catalog-text {
      padding-left: 5px;
      &.high {
        color: rgb(0, 125, 241);
      }
    }
  }
}
</style>
